.is-visible .cd-handle {
  opacity: 1;
  -webkit-transform: translate3d(0, 0, 0) scale(1);
  -moz-transform: translate3d(0, 0, 0) scale(1);
  -ms-transform: translate3d(0, 0, 0) scale(1);
  -o-transform: translate3d(0, 0, 0) scale(1);
  transform: translate3d(0, 0, 0) scale(1);
  -webkit-transition: -webkit-transform 0.3s 0.7s, opacity 0s 0.7s;
  -moz-transition: -moz-transform 0.3s 0.7s, opacity 0s 0.7s;
  transition: transform 0.3s 0.7s, opacity 0s 0.7s; }

/* Reviews Section */
.reviews-heading {
  display: flex;
  align-items: baseline;
  justify-content: space-between; }
  .reviews-heading .heading {
    display: inline-flex;
    align-items: baseline; }
    .reviews-heading .heading h4 {
      padding-right: 0.75rem; }

.review-container {
  padding-right: 2.5rem; }
  .review-container .review-details {
    padding: 0.75rem 0;
    display: flex;
    justify-content: space-between;
    align-items: baseline; }
    .review-container .review-details .name {
      font-weight: bold; }
    .review-container .review-details .date {
      color: #96AAB9;
      font-size: 0.875rem; }

.third-review {
  padding-right: 0; }

@media screen and (max-width: 1080px) {
  .review-container {
    padding-right: 1.25rem; }
  .third-review {
    display: none; } }

@media screen and (max-width: 770px) {
  .review-container {
    padding-right: 0; }
  .third-review {
    display: block; } }

/* Lens Installation Video Section */
.lens-installation-video {
  border-top: 1px solid #D3DAE8; }
  .lens-installation-video .video-container video {
    max-width: 100%;
    height: auto;
    outline: 0; }
